/*** LAYOUT ***/
/*** FONTS ***/
/*** COLOURS ***/
.u-centerText {
  text-align: center; }

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

html {
  font-size: 62.5%; }
  @media (min-width: 480px) {
    html {
      font-size: 80%; } }
  @media (min-width: 900px) {
    html {
      font-size: 100%; } }

img {
  width: 100%;
  display: block; }

ul {
  padding-left: 0; }

body {
  font-family: "Solway", serif;
  font-size: 1.6rem;
  background: url(../../assets/paper_fibers.png); }

#root {
  min-height: 100vh;
  display: flex;
  flex-direction: column; }

main {
  flex-grow: 1;
  display: flex;
  flex-direction: column; }

a {
  text-decoration: none;
  color: #236893; }
  a:hover {
    color: #2C8CAF; }

.button {
  font-size: 2rem;
  padding: .8rem .4rem;
  background-color: #e6726d;
  color: #FFF;
  border: 2px solid #935327;
  min-width: 10rem;
  position: relative; }
  .button:not([disabled]) {
    transition: .2s transform; }
    .button:not([disabled]):hover, .button:not([disabled]):focus {
      border: 1px solid #000;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
      transform: translateY(-5px); }
  .button[disabled] {
    background-color: #542614;
    color: #74311C; }

.AppTitle {
  display: inline-block;
  position: relative;
  padding-bottom: 3rem; }
  .AppTitle__heading {
    font-family: "Hind Siliguri", sans-serif;
    font-weight: 700;
    font-size: 2.4rem;
    text-transform: uppercase;
    margin: 0;
    background: #FFF;
    line-height: .8;
    z-index: 10; }
    .AppTitle__heading::before {
      content: "Colour in the Lines";
      display: block;
      position: absolute;
      top: 9;
      z-index: -1;
      transition: 2s all ease-in-out;
      transform: scaleY(1);
      text-shadow: 0 0.06em 0 #FFF, 0 0.12em 0 #000, 0 0.16em 0 #FFF, 0 0.2em 0 #000, 0 0.24em 0 #FFF, 0 0.28em 0 #000, 0 0.32em 0 #FFF, 0 0.36em 0 #000; }
    .AppTitle__heading--expand::before {
      transform: scaleY(1.2);
      transform-origin: top; }

.PalettesSelection__container {
  display: flex;
  flex-flow: row wrap; }

.PalettesSelection__button {
  flex: 1 1 50%; }
  .PalettesSelection__button--right {
    text-align: right; }

.Palette {
  list-style: none;
  order: -1;
  width: 100%; }

.ColourButton {
  border: 2px solid transparent;
  width: 100%;
  height: 5rem; }
  .ColourButton:hover, .ColourButton:focus {
    border: 2px solid #e6726d; }

.wrapper {
  max-width: 114rem;
  width: 90%;
  margin: 0 auto; }

.flexGrid {
  display: flex;
  flex-flow: row wrap; }
  .flexGrid--noWrap {
    display: flex;
    justify-content: center; }

.col-1-of-3 {
  flex: 1 1 33%; }

.col-1-of-2 {
  flex: 1 1 50%; }
  .col-1-of-2--lg {
    flex: 1 1 100%; }
    @media (min-width: 900px) {
      .col-1-of-2--lg {
        flex: 1 1 50%; } }

.HeaderComponent {
  padding: 4.8rem 1.2rem 0; }
  @media (min-width: 1240px) {
    .HeaderComponent {
      padding: 1.2rem 0 0; } }

.FormComponent__label {
  margin-bottom: 1rem; }

.FormComponent__form {
  display: flex;
  flex-direction: column;
  align-items: center; }

.FormComponent__textarea {
  resize: none;
  line-height: 1.6;
  padding: 1.6rem;
  border: 2px solid #000; }
  .FormComponent__textarea:focus {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.6); }

.FormComponent__button {
  margin: 1.6rem 0; }

.FormComponent__error-container {
  background-color: black;
  color: white;
  padding: 1.6rem;
  position: relative; }

.FormComponent__error {
  text-align: center; }

.FormComponent__error-button {
  position: absolute;
  right: 0;
  top: 0;
  background-color: transparent;
  color: #FFF;
  border: 0; }

.FooterComponent {
  font-size: .8rem;
  padding-top: 1.2rem;
  text-align: center; }
  .FooterComponent__container {
    align-items: center; }
  .FooterComponent__personalInfo {
    text-align: right; }

.FooterCredits__list {
  list-style: none;
  display: flex;
  align-items: center; }

.FooterCredits__item {
  position: relative; }
  .FooterCredits__item:not(:last-child) {
    margin-right: 2rem; }
